import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { signOut, onAuthStateChanged } from 'firebase/auth'; // Make sure to import from 'firebase/auth'
import { auth } from '../firebase'; // Make sure this points to your firebase.js
import coachingFinderLogo from './images/ChatGPT.png'; 

const Navbar = () => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true); 
  const [showDropdown, setShowDropdown] = useState(false); 
  const navigate = useNavigate();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setUser(user);
      setLoading(false); 
    });
    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    try {
      await signOut(auth);
      navigate('/login');
    } catch (error) {
      console.error('Error logging out:', error);
    }
  };

  if (loading) return null; 

  return (
    <nav className="bg-gray-800 p-4">
      <div className="flex items-center justify-between">
        <Link to="/" className="flex items-center space-x-2 text-white text-2xl font-bold">
          <img src={coachingFinderLogo} alt="Coaching Finder Logo" className="w-8 h-8" />
          <span>Coaching Finder</span>
        </Link>

        <div className="flex items-center bg-white p-2 rounded-md shadow-md w-1/4">
          <input type="text" placeholder="Search Institutes" className="w-full px-2 py-1 rounded-md" />
          <button className="bg-blue-500 text-white px-4 py-2 rounded-md ml-2">Search</button>
        </div>

        <div className="flex items-center space-x-6 text-white">
          <Link to="/" className="hover:text-gray-300">Home</Link>
          <Link to="/about" className="hover:text-gray-300">About</Link>
          <Link to="/contact" className="hover:text-gray-300">Contact Us</Link>

          {!user ? (
            <Link to="/login" className="hover:text-gray-300">Login</Link>
          ) : (
            <div className="relative">
              <div className="flex items-center cursor-pointer" onClick={() => setShowDropdown(!showDropdown)}>
                <img src={user.photoURL || 'https://via.placeholder.com/40'} alt="Profile" className="w-8 h-8 rounded-full" />
                <span className="ml-2">{user.displayName || 'User'}</span>
              </div>

              {showDropdown && (
                <div className="absolute right-0 bg-white text-black mt-2 rounded shadow-lg py-2 w-48 z-50">
                  <div className="px-4 py-2 border-b">{user.email}</div>
                  <button onClick={handleLogout} className="w-full text-left px-4 py-2 hover:bg-red-100 text-red-600">Logout</button>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
